@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,700&display=swap&subset=cyrillic);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes appearance{
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes appearance{
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@-webkit-keyframes rotation{
    0%{
        opacity: 0;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }
    50% {
        opacity: 0.5;
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg)
    }
    100%{
        opacity: 0.5;
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}

@keyframes rotation{
    0%{
        opacity: 0;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }
    50% {
        opacity: 0.5;
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg)
    }
    100%{
        opacity: 0.5;
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}

@-webkit-keyframes up-moving{
    0%{
        margin-top: 100%;
    }
    100%{
        margin-top: 0%;
    }
}

@keyframes up-moving{
    0%{
        margin-top: 100%;
    }
    100%{
        margin-top: 0%;
    }
}


@-webkit-keyframes down-moving{
    100%{
        margin-top: 20%;
        opacity: 0; 
    }
    0%{
        margin-top: 0%;
        opacity: 1;
    }
}


@keyframes down-moving{
    100%{
        margin-top: 20%;
        opacity: 0; 
    }
    0%{
        margin-top: 0%;
        opacity: 1;
    }
}


.accept-button-up{
    margin-top: 20%;
    -webkit-animation: up-moving 0.8s ease forwards;
            animation: up-moving 0.8s ease forwards;
}

.accept-button-down{
    margin-top: 0%;
    -webkit-animation: down-moving 1s ease forwards;
            animation: down-moving 1s ease forwards;
}

.start-page{
    background: -webkit-linear-gradient(294.07deg, #F49D91 9.92%, rgba(237, 87, 177, 0.54) 92.29%);
    background: linear-gradient(155.93deg, #F49D91 9.92%, rgba(237, 87, 177, 0.54) 92.29%);
    width: 100%;
    height: 100vh;
    position: absolute;
    text-align: center;
}

.text{
    font-family: 'Montserrat', sans-serif;
}

.header-text{
    font-size: 18px;
    text-align: center;
    margin-top: 20vh;
    -webkit-animation: appearance 2s ease;
            animation: appearance 2s ease;
}

.text_bold{
    font-weight: bold;
}

.small-text{
    font-size: 13px;
    margin-top: 2vh;
}

.items{
    display: grid;
    grid-template-rows: repeat(2,auto);
    grid-template-columns: repeat(2,auto);
    justify-items: center;
    justify-content: center;
    align-content: start;
    grid-gap: 45px;
    width: 100vw;
    height: 50vh;
    margin-top: 6vh;
}

.square-item{
    height: 90px;
    width: 90px;
    border-radius: 30px;
}

.item_color_red{
    background: #DD2B2B;
}

.item_color_red_anim{
    -webkit-animation: rotation 1s linear;
            animation: rotation 1s linear;
}

.item_color_orange{
    background: #FF9900;
    
}

.item_color_orange_anim{
    -webkit-animation: rotation 1s linear 0.2s;
            animation: rotation 1s linear 0.2s; 
}

.item_color_blue{
    background: #43AEEB;
    
}
.item_color_blue_anim{
    -webkit-animation: rotation 1s linear 0.4s;
            animation: rotation 1s linear 0.4s;
}

.item_color_green{
    background: #1FBD97;
}

.item_color_green_anim{
    -webkit-animation: rotation 1s linear 0.6s;
            animation: rotation 1s linear 0.6s;
}

.item-icon{
    height: 50px;
    width: 50px;
    padding: 20px;
}

.item_choose_false{
    opacity: 0.5;
}

.item_choose_true{
    opacity: 1;
}

.link{
    color: aliceblue;
    text-decoration: none;
    font-size: 18px;
    display: inline-block;
    height: 40px;
    width: 50vw;
    margin-top:9px;
}

.button{
    background-color: #1FBD97;
    height: 40px;
    width: 50vw;
    margin-left: 25vw;
    text-align: center;
    vertical-align: middle;
    border-radius: 30px;
}

.button:active{
    background-color: #3DA38A;
}

.item_color_white{
    background-color: #EEE5E5;
}

.item_color_light-orange{
    background-color: #F6CB77;
}

.item_color_light-green{
    background-color: #B2F493;
}

.item_color_light-red{
    background-color: #F26D50;
}
.slider{
    padding: 0%;
    margin: 0%;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    position: absolute;
    width: 30vh;
    top: 50vh;
    left: 25vw;
    height: 25px;
    border-radius: 10px;
    background: -webkit-gradient(linear, left top, right top, from(#C7BFF7), to(#B7E8B3));
    background: -webkit-linear-gradient(left, #C7BFF7 0%, #B7E8B3 100%);
    background: linear-gradient(90deg, #C7BFF7 0%, #B7E8B3 100%);
    outline: none;
}

.slider::-webkit-slider-thumb{
    -webkit-appearance: none;
            appearance: none;
    background-color: aliceblue;
    width: 30px;
    height: 30px;
    border-radius: 50%;
}

.slider-value{
    line-height: 5vh;
    position: absolute;
    left: 45vw;
    top: 44vh;
    font-size: 15vh;
    font-weight: bold;
}

.slider-value > span{
    font-size: 4vh;
}

.accept-button{
    position: absolute;
    margin-top: 60vh;
}
.leaflet-interactive {
  stroke: pink;
  stroke-opacity: 1;
}

.leaflet-routing-container {
  display: none;
}

@-webkit-keyframes move-tinder-up{
    0%{
        margin-top: 81vh;
    }
    100%{
        margin-top: 0vh;
    }
}

@keyframes move-tinder-up{
    0%{
        margin-top: 81vh;
    }
    100%{
        margin-top: 0vh;
    }
}

@-webkit-keyframes move-tinder-down{
    100%{
        margin-top: 81vh;
    }
}

@keyframes move-tinder-down{
    100%{
        margin-top: 81vh;
    }
}

@-webkit-keyframes move-button-up{
    0%{
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg)
    }
    100%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }
}

@keyframes move-button-up{
    0%{
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg)
    }
    100%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }
}

@-webkit-keyframes move-button-down{
    100%{
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg)
    }
}

@keyframes move-button-down{
    100%{
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg)
    }
}

@-webkit-keyframes move-tinder-new{
    0%{
        margin-top: 100vh;
    }
    50%{
        margin-top: 75vh;
    }
    100%{
        margin-top: 81vh;
    }
}

@keyframes move-tinder-new{
    0%{
        margin-top: 100vh;
    }
    50%{
        margin-top: 75vh;
    }
    100%{
        margin-top: 81vh;
    }
}


.tinder-card{
    width: 80vw;
    max-width: 60vh;
    height: 80vh;
    background: #f1f1f1;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 10vh;
    border-radius: 30px;
    
}

.tinder-card-new{
    -webkit-animation: move-tinder-new 0.9s forwards;
            animation: move-tinder-new 0.9s forwards;
}
.tinder-card-down{
    -webkit-animation: move-tinder-down 2s forwards;
            animation: move-tinder-down 2s forwards;
}

.tinder-card-up{
    -webkit-animation: move-tinder-up 1s forwards;
            animation: move-tinder-up 1s forwards;
}

.move-button-down{
    -webkit-animation: move-button-down 0.8s forwards;
            animation: move-button-down 0.8s forwards;
}

.move-button-up{
    -webkit-animation: move-button-up 0.8s forwards;
            animation: move-button-up 0.8s forwards;
}


.move-button{
    position: absolute;
    width: 7%;
    height: 7%;
    margin-left: 85%;
    margin-top: 5%; 
}

.tinder-card-header{
    position: absolute;
    margin-top: 7%;
    text-align: left;
    margin-left: 10%;
    font-size: 20px;
    font-weight: bold;
    padding: 0%;
    margin-bottom: 5%;
}

.place-info{
    display: grid;
    margin-top: 8%;
    grid-template-columns: 1fr 4fr;
    grid-template-rows: 50px 1fr;
    margin-left: 5%;
    margin-right: 5%;
}

.typeof-place{
    padding: 10px;
    border-radius: 50%;
    justify-self: center;
    height: 50%;
}

.nameof-place{
    height: 5%;
    font-weight: 300;
    padding-left: 8%;
}

.desc-place{
    padding-left: 8%;
    font-size: 12px;
    padding-right: 8%;
}

.line{
    height: 1px;
    width: 100%;
    background-color: rgb(156, 156, 156);
    margin-top:20%;
}
.scrolling{
    position: fixed;
    overflow: scroll;
    height: 67vh;
}

.monument{
    background-color: rgb(100, 174, 235);
}

.museum{
    background: orange;
}



